import React, { useState, useContext } from 'react';
import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import Card from '../components/common/Card';
import Hyperlink from './../components/common/Hyperlink';
import Label from './../components/common/Label';
import FormInput from './../components/FormInput';
import FormSuccess from './../components/FormSuccess';
import FormError from './../components/FormError';
import GradientBar from './../components/common/GradientBar';
import { AuthContext } from '../context/AuthContext';
import { publicFetch } from './../util/fetch';
import { Redirect } from 'react-router-dom';
import GradientButton from '../components/common/GradientButton';
import logo from '../assets/images/logo-santex.png';
import { Link } from 'react-router-dom';

const LoginSchema = Yup.object().shape({
  email: Yup.string().required('Email is required'),
  password: Yup.string().required('Password is required')
});

const Login = () => {
  const authContext = useContext(AuthContext);
  const [loginSuccess, setLoginSuccess] = useState();
  const [loginError, setLoginError] = useState();
  const [redirectOnLogin, setRedirectOnLogin] = useState(
    false
  );
  const [loginLoading, setLoginLoading] = useState(false);

  const submitCredentials = async credentials => {
    try {
      setLoginLoading(true);
      const { data } = await publicFetch.post(
        `authenticate`,
        credentials
      );

      authContext.setAuthState(data);
      setLoginSuccess(data.message);
      setLoginError(null);

      setTimeout(() => {
        setRedirectOnLogin(true);
      }, 700);
    } catch (error) {
      setLoginLoading(false);
      const { data } = error.response;
      setLoginError(data.message);
      setLoginSuccess(null);
    }
  };

  return (
    <>
      {redirectOnLogin && <>{authContext.isAdmin()?<Redirect to="/dashboard" />:<Redirect to="/products" />}</>}
      <div className="w-full top-0 bg-white px-5 p-3 lg:fixed z-20">
        <div className="flex justify-left">
          <img
            className="w-40 h-full"
            src={logo}
            alt="Logo"
          />
          <div className="flex items-center ml-auto font-nucleo text-xl">
            <Link
              to="/"
              className="text-gradient mr-6"
            >
              Home
            </Link>
          </div>
        </div>
        <GradientBar />
      </div>
      <div className="w-full bg-image" >
        <section className="sm:w-1/2 h-auto m-auto sm:pt-3 sm:pb-10">
          <div className=" h-screen mt-32">
          <Card >
            <div className="flex items-center justify-center py-5 px-4 sm:px-6 lg:px-8">
              <div className="max-w-md w-full">
                <div>
                  <div className="w-48 m-auto mb-6">
                    <img src={logo} alt="Logo" />
                  </div>
                  <h2 className="mb-2 text-center text-3xl leading-9 font-extrabold text-gray-900 font-nucleo">
                    Log in to your account
                  </h2>
                  <p className="text-gray-600 text-center font-serif">
                    Don't have an account?{' '}
                    <Hyperlink
                      to="signup"
                      text="Sign up now"
                    />
                  </p>
                </div>

                <Formik
                  initialValues={{
                    email: '',
                    password: ''
                  }}
                  onSubmit={values =>
                    submitCredentials(values)
                  }
                  validationSchema={LoginSchema}
                >
                  {() => (
                    <Form className="mt-8">
                      {loginSuccess && (
                        <FormSuccess text={loginSuccess} />
                      )}
                      {loginError && (
                        <FormError text={loginError} />
                      )}
                      <div>
                        <div className="mb-2">
                          <div className="mb-1">
                            <Label text="Email" />
                          </div>
                          <FormInput
                            ariaLabel="Email"
                            name="email"
                            type="text"
                            placeholder="Email"
                          />
                        </div>
                        <div>
                          <div className="mb-1">
                            <Label text="Password" />
                          </div>
                          <FormInput
                            ariaLabel="Password"
                            name="password"
                            type="password"
                            placeholder="Password"
                          />
                        </div>
                      </div>

                      <div className="mt-6 flex justify-start">
                        <div className="text-sm leading-5 font-nucleo">
                          <Hyperlink
                            to="forgot-password"
                            text="Forgot your password?"
                          />
                        </div>
                      </div>

                      <div className="mt-6 float-right font-nucleo">
                        <GradientButton
                          type="submit"
                          text="Log In"
                          loading={loginLoading}
                        />
                      </div>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </Card>
          </div>
        </section>
      </div>
    </>
  );
};

export default Login;
