import React, { lazy, Suspense, useContext } from "react";
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect,
  HashHistory
} from "react-router-dom";
import "./assets/stylesheets/App.css";

import { AuthProvider, AuthContext } from "./context/AuthContext";
import { FetchProvider } from "./context/FetchContext";
import {ProductProvider } from "./context/ProductContext";

import AppShell from "./AppShell";

import Home from "./pages/Home";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import FourOFour from "./pages/FourOFour";

const Clients = lazy(() => import("./pages/Clients"));
const Dashboard = lazy(() => import("./pages/DashBoard"));
const Admin = lazy(() => import("./pages/Admin"));
const Products = lazy(() => import("./pages/Products"));
const Catalogue = lazy(() => import("./pages/Catalogue"));
const ShoppingCart = lazy(() => import("./pages/ShoppingCart"));
const Orders = lazy(() => import("./pages/Orders"));

const LoadingFallback = () => (
  <AppShell>
    <div className="p-4">Loading...</div>
  </AppShell>
);

const UnauthenticatedRoutes = () => (
  <Switch>
    <Route path="/login">
      <Login />
    </Route>
    <Route path="/signup">
      <Signup />
    </Route>
    <Route exact path="/catalogue">
      <Catalogue />
    </Route>
    <Route exact path="/">
      <Home />
    </Route>
    <Route path="*">
      <FourOFour />
    </Route>
  </Switch>
);

const AuthenticatedRoute = ({ children, ...rest }) => {
  const auth = useContext(AuthContext);
  return (
    <Route
      {...rest}
      render={() =>
        auth.isAuthenticated() ? (
          <AppShell>{children}</AppShell>
        ) : (
          <Redirect to="/" />
        )
      }
    ></Route>
  );
};

const AppRoutes = () => {
  const auth = useContext(AuthContext);
  return (
    <>
      <Suspense fallback={<LoadingFallback />}>
        <Switch>
          <AuthenticatedRoute path="/admins">
            {auth.isAdmin() ? (
              <Admin />
            ) : (
              <>
                <h1 className="text-center mt-10 text-2xl">Not authorized</h1>
              </>
            )}
          </AuthenticatedRoute>
          <AuthenticatedRoute path="/dashboard">
            {auth.isAdmin() ? (
              <Dashboard />
            ) : (
              <>
                <h1 className="text-center mt-10 text-2xl">Not authorized</h1>
              </>
            )}
          </AuthenticatedRoute>
          <AuthenticatedRoute path="/clients">
            {auth.isAdmin() ? (
              <Clients />
            ) : (
              <>
                <h1 className="text-center mt-10 text-2xl">Not authorized</h1>
              </>
            )}
          </AuthenticatedRoute>
          <AuthenticatedRoute path="/products">
            <Products />
          </AuthenticatedRoute>
          <AuthenticatedRoute path="/shopping">
            <ShoppingCart />
          </AuthenticatedRoute>
          <AuthenticatedRoute path="/orders">
            <Orders />
          </AuthenticatedRoute>
          <UnauthenticatedRoutes />
        </Switch>
      </Suspense>
    </>
  );
};

function App() {
  return (
    <>
      <Router basename={process.env.PUBLIC_URL}>
        <AuthProvider>
          <FetchProvider>
            <ProductProvider>
              <div className="bg-white">
                <AppRoutes />
              </div>
            </ProductProvider>
          </FetchProvider>
        </AuthProvider>
      </Router>
    </>
  );
}

export default App;
