import React, { createContext, useState, useContext } from "react";
import { useHistory } from "react-router-dom";

const ProductContext = createContext();
const { Provider } = ProductContext;

const ProductProvider = ({ children }) => {
  const [productState, setProductState] = useState({
    products: [],
  });

  const setProductInfo = ({ action, payload }) => {
    switch (action) {
      case "define":
        setProductState({ products: payload });
        break;
      case "add":
        setProductState(({ products }) => {
          return { products: [...products, payload] };
        });
        break;
      case "remove":
        setProductState(({ products }) => {
          return {
            products: [...products].filter(
              (product) => product._id !== payload._id
            ),
          };
        });
        break;
      case "edit":
        setProductState(({ products }) => {
          return {
            products: [...products].map((product) => {
              if (product._id !== payload._id) {
                return payload;
              } else {
                return product;
              }
            }),
          };
        });
        break;
      default:
    }
  };

  return (
    <Provider
      value={{
        productState,
        setProductInfo,
      }}
    >
      {children}
    </Provider>
  );
};

const useProducts = () => {
  const { productState, setProductInfo } = useContext(ProductContext);
  const { products } = productState;
  return { products, setProductInfo };
};

export { ProductContext, ProductProvider, useProducts };
