import React, { createContext, useState, useContext } from "react";
import { useHistory } from "react-router-dom";

const AuthContext = createContext();
const { Provider } = AuthContext;

const AuthProvider = ({ children }) => {
  const history = useHistory();

  const token = localStorage.getItem("token");
  const userInfo = localStorage.getItem("userInfo")
    ? JSON.parse(localStorage.getItem("userInfo"))
    : localStorage.getItem("userInfo");
  const expiresAt = localStorage.getItem("expiresAt");
  const productsCart = localStorage.getItem("productsCart")
    ? JSON.parse(localStorage.getItem("productsCart"))
    : localStorage.getItem("productsCart");
  const productsLike = localStorage.getItem("productsLike")
    ? JSON.parse(localStorage.getItem("productsLike"))
    : localStorage.getItem("productsLike");

  const [authState, setAuthState] = useState({
    token,
    expiresAt,
    userInfo: userInfo ? userInfo : {},
    productsCart: productsCart ? Array.from(productsCart) : [],
    productsLike: productsLike ? Array.from(productsLike) : [],
  });

  const setAuthInfo = ({
    token,
    userInfo,
    expiresAt,
    productsCart,
    productsLike,
  }) => {
    localStorage.setItem("token", token);
    localStorage.setItem(
      "userInfo",
      userInfo ? JSON.stringify(userInfo) : userInfo
    );
    localStorage.setItem("expiresAt", expiresAt);
    localStorage.setItem(
      "productsCart",
      productsCart !== undefined ? productsCart : []
    );
    localStorage.setItem(
      "productsLike",
      productsLike !== undefined ? productsLike : []
    );

    setAuthState({
      token,
      userInfo,
      expiresAt,
      productsCart,
      productsLike,
    });
  };

  const setOrders = (orders) => {
    let savedUserInfo = localStorage.getItem("userInfo")
      ? localStorage.getItem("userInfo")
      : undefined;
    if (savedUserInfo) {
      console.log(JSON.stringify(savedUserInfo));
      savedUserInfo = JSON.parse(savedUserInfo);
      if (savedUserInfo.orders && savedUserInfo.orders.length) {
        savedUserInfo.orders.push(orders);
        localStorage.setItem("userInfo", JSON.stringify(savedUserInfo));
      } else {
        savedUserInfo.order = [orders];
        localStorage.setItem("userInfo", JSON.stringify(savedUserInfo));
      }
    }
    localStorage.setItem("order", JSON.stringify(orders));
    setAuthState((state) => ({ ...state, order: orders }));
  };
  const getCurrentOrder = () => {
    const currentOrder = localStorage.getItem("order")
      ? JSON.parse(localStorage.getItem("order"))
      : undefined;
    if (currentOrder) {
      return currentOrder;
    } else {
      return [];
    }
  };
  const setProductsCart = (productsCart) => {
    localStorage.setItem("productsCart", JSON.stringify(productsCart));
    setAuthState((state) => ({ ...state, productsCart: productsCart }));
  };
  const setProductsLike = (productsLike) => {
    localStorage.setItem("productsLike", JSON.stringify(productsLike));
    setAuthState((state) => ({ ...state, productsLike: productsLike }));
  };

  const removeProductCartItem = (id) => {
    setAuthState((state) => {
      if (id && state.productsCart && state.productsCart.length) {
        const mutableProductCart = [...state.productsCart];
        const newProductCart = mutableProductCart.filter((p) => p._id !== id);
        localStorage.setItem("productsCart", JSON.stringify(newProductCart));
        return {
          ...state,
          productsCart: newProductCart,
        };
      } else {
        return state;
      }
    });
  };

  const removeAllProductsCartItems = () => {
    setAuthState((state) => {
      localStorage.removeItem("productsCart");
      return { ...state, productsCart: [] };
    });
  };

  const logout = () => {
    localStorage.clear();
    setAuthState({});
    history.push("/login");
  };

  const isAuthenticated = () => {
    if (!authState.token || !authState.expiresAt) {
      return false;
    }
    return new Date().getTime() / 1000 < authState.expiresAt;
  };

  const isAdmin = () => {
    return authState.userInfo && authState.userInfo.role
      ? authState.userInfo.role === "ADMIN"
      : false;
  };

  return (
    <Provider
      value={{
        authState,
        setAuthState: (authInfo) => setAuthInfo(authInfo),
        logout,
        isAuthenticated,
        isAdmin,
        setProductsCart,
        setProductsLike,
        removeProductCartItem,
        setOrders,
        getCurrentOrder,
        removeAllProductsCartItems,
      }}
    >
      {children}
    </Provider>
  );
};

export const useAuth = () => {
  const {
    authState,
    setAuthState,
    logout,
    isAuthenticated,
    isAdmin,
    setProductsCart,
    setProductsLike,
    removeProductCartItem,
    setOrders,
    getCurrentOrder,
    removeAllProductsCartItems,
  } = useContext(AuthContext);
  return {
    authState,
    setAuthState,
    logout,
    isAuthenticated,
    isAdmin,
    setProductsCart,
    setProductsLike,
    removeProductCartItem,
    setOrders,
    getCurrentOrder,
    removeAllProductsCartItems,
  };
};

export { AuthContext, AuthProvider };
