import React, { useContext } from 'react';
import classNames from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faUserCog,
  faUsers,
  faShoppingCart,
  faUserMd,
  faClipboardList,
  faCogs,
  faFileInvoiceDollar
} from '@fortawesome/free-solid-svg-icons';
import { Link, useLocation } from 'react-router-dom';
import logo from '../assets/images/logo-santex.png';
import { AuthContext } from '../context/AuthContext';

const navItems = [
  
  {
    label: 'Products',
    path: 'products',
    icon: faUserMd,
    allowedRoles: ['USER']
  },
  {
    label: 'Shopping',
    path: 'shopping',
    icon: faShoppingCart,
    allowedRoles: ['USER']
  },
  {
    label: 'Orders',
    path: 'orders',
    icon: faFileInvoiceDollar,
    allowedRoles: ['USER']
  },
  {
    label: 'dashboard',
    path: 'dashboard',
    icon: faCogs,
    allowedRoles: ['ADMIN']
  },
  {
    label: 'Orders',
    path: 'orders',
    icon: faClipboardList,
    allowedRoles: ['ADMIN']
  },
  {
    label: 'Admins',
    path: 'admins',
    icon: faUserCog,
    allowedRoles: ['ADMIN']
  },
  {
    label: 'Clients',
    path: 'clients',
    icon: faUsers,
    allowedRoles: ['ADMIN']
  }
  
];

const NavItem = ({ navItem }) => {
  const location = useLocation();
  const isCurrentRoute =
    location.pathname === `/${navItem.path}`;
  const classes = classNames({
    'px-2 sm:px-6 justify-center sm:justify-start py-3 rounded-full flex': true,
    'text-gray-600 hover:text-gray-900 transform hover:translate-x-1 transition ease-in-out duration-100': !isCurrentRoute,
    'bg-gradient text-gray-100 shadow-lg': isCurrentRoute
  });
  return (
    <Link to={navItem.path} className={classes}>
      <div className="flex items-center">
        <div className="mr-0 sm:mr-4">
          <FontAwesomeIcon icon={navItem.icon} />
        </div>
        <span className="hidden sm:block">
          {navItem.label}
        </span>
      </div>
    </Link>
  );
};

const NavItemContainer = ({ children }) => (
  <div>{children}</div>
);

const Sidebar = () => {
  const auth = useContext(AuthContext);
  const { role } = auth.authState.userInfo;
  return (
    <section className="h-screen">
      <div className="md:w-40 sm:w-24 xl:w-48 m-auto">
        <img src={logo} rel="logo" alt="Logo" />
      </div>
      <div className="text-center">
        <span className="text-black font-nucleo">Innovating every day to be better ¡¡¡</span>
      </div>
      <div className="mt-20 font-nucleo">
        {navItems.map((navItem, i) => (
          <div className="mt-3" key={i}>
            <NavItemContainer>
              {navItem.allowedRoles.includes(role) && (
                <NavItem navItem={navItem} />
              )}
            </NavItemContainer>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Sidebar;
